      <div></div>
      <h2 class="accordion-header" v-bind:id="'panelsStayOpen-heading'+i">
        <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
          v-bind:data-bs-target="'#panelsStayOpen-collapse'+i" aria-expanded="true"
          v-bind:aria-controls="'panelsStayOpen-collapse'+i">
          <div class="titlecontent"> {{ i+1 }} {{question.title}}  </div>
        </button>
      </h2>

      <div v-bind:id="'panelsStayOpen-collapse'+i" class="accordion-collapse collapse"
        v-bind:aria-labelledby="'panelsStayOpen-heading'+i">
        <h1 hidden>{{state.nickname = question.nickname}}</h1>
        <h1 hidden>{{state.circleid = question.circleid}}</h1>

        <div class="buttongoup">
          <el-button-group class="ml-4">
            <el-button class="titlebtn" data-bs-toggle="modal" data-bs-target="#staticBackdrop1"
              @click="sendId(question.questionid, question.nickname, question.title, question.content)"> <span> 
                질문 수정 </span></el-button>
            <el-button class="titlebtn" @click="deleteModal"> <span> 삭제 </span> </el-button>
          </el-button-group>
        </div>



        <!-- <div class="switch">
          <label class="publiclabel"> 공개여부 ::  </label>
          <el-switch  v-model="state.questionPublic" active-text="public" inactive-text="private"  disabled />
        </div> -->

        <div class="accordion-body">
          <p hidden>{{ state.qid=question.questionid }} </p>
          <p><b>작성자 :: </b> {{ question.nickname }} </p>
          <p><b>질문 내용 :: </b>{{ question.content }} </p>
          <p><b>수정 일자 :: </b>{{formatDate(question.modDate)}} </p>
        </div>

        <!-- Answer -->
        <div class="answerList">
          <Answer :questionid="question.questionid" :qnickname="question.nickname" :quid="question.uid" />
        </div>

