    </div>
    <!--accordion-item question-->
  </div>
  <!--arccodian-->

  <!-- Modal -->
  <div class="modal fade" id="staticBackdrop1" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1"
    aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="staticBackdropLabel">질문 수정하기</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>

        <div class="modal-body container">

          <!-- <div class="item modalswitch">
            <label for="nickname"> 공개여부 :: </label><br>
            <el-switch v-model="questionPublic" size="large" active-text="Public" />
          </div>

          <div class="item">
            <label for="nickname"> Nickname :: </label><br>
            <input type="text" id="nickname" v-model="modal.nickname" disabled>
          </div> -->

          <div class="item">
            <label for="title"> 질문 제목 :: </label><br>
            <input type="text" id="title" v-model="modal.title" style="width:400px;" >
          </div>

          <div class="item">
            <label for="content"> 질문 내용 :: </label><br>
            <textarea type="text" id="content" v-model="modal.content"></textarea>
          </div>
        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">닫기</button>
          <button type="button" class="btn btn-primary" @click="modifyQuestion()"
            data-bs-dismiss="modal">수정하기</button>
        </div>
      </div>
    </div>
  </div>


  <!-- Register -->
  <Teleport to="body">
    <!-- use the modal component, pass in the prop -->
    <modal :show="state.showModal" @close="state.showModal=false">
      <template #header>
        <h3>Register Question</h3>
      </template>
      <template #body>

        <div class="item modalswitch">
          <label for=""> 공개여부 :: </label><br>
          <el-switch size="large" active-text="Public" v-model="questionPublic" />
        </div>

        <div class="item">
          <label for=""> 닉네임 :: </label><br>
          <input type="text" v-model="state.nickname" disabled>
          <input type="text" v-model="state.circleid" disabled>
        </div>

        <div class="item">
          <label for="title"> 질문 제목 :: </label><br>
          <input type="text" id="title" v-model="regist.registTitle" style="width:400px;">
        </div>

        <div class="item">
          <label for="content"> 질문 내용 :: </label><br>
          <textarea type="text" id="content" v-model="regist.registContent"></textarea>
        </div>

      </template>
      <template #footer>
        <el-button type="secondary" plain @click="state.showModal=false"> Close </el-button>
        <el-button type="primary" plain @click="questionRegister()">Register</el-button>
      </template>
    </modal>
  </Teleport>



</template>


<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { onMounted, reactive, ref } from 'vue'
import { useRouter } from 'vue-router';
import Modal from './teleport/Modal.vue'
import axios from 'axios'
import dayjs from 'dayjs'
import Answer from './Answer.vue'



export default {
  name: "TheQuestion",
  components: { Answer, Modal },
  setup() {

    const myoption = ref('title')
    const string2search = ref('')
    const launchSearch = async () => {
      const url = "http://localhost:9090/api/searchquestionbyemail";
      const headers = {
        "Content-Type": "application/json",
        Authorization: sessionStorage.getItem('TOKEN'),
        "token": sessionStorage.getItem('TOKEN')
      };

      const body = {
        email: state.email,
        myoption: myoption.value,
        string2search: string2search.value
      };
      await axios.post(url, body, { headers }).then(function (res) {
        state.lists = res.data
      }).catch((error) => { console.log("the error :" + error) })
    };

    onMounted(() => { questionlistbyemail() })

    const questionPublic = ref(true)
    const router = useRouter();

    const state = reactive({
      qid: '',
      uid: '',
      circleid: '',
      nickname: '',
      questionPublic: '',
      lists: '',
      modiDate: '',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    });

    const formatDate = (dateString) => {
      const date = dayjs(dateString);
      return date.format('YYYY-MM-D HH:mm');
    };

    const modal = reactive({
      questionid: '',
      nickname: '',
      title: '',
      content: ''
    })

    const sendId = (questionid, nickname, title, content) => {
      modal.questionid = questionid
      modal.nickname = nickname
      modal.title = title
      modal.content = content
    };

    //delete Modal
    const deleteModal = () => {
      ElMessageBox.confirm(
        '질문을 삭제하시겠습니까?',
        '알람',
        {
          confirmButtonText: '예',
          cancelButtonText: '취소',
          type: 'warning',
        }
      )
        .then(() => {
          ElMessage({
            type: 'success',
            message: 'Delete completed',
          })
          deleteQuestion()
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: 'Delete canceled',
          })
        })
    }

    const questionlistbyemail = async () => {
      const url = "http://localhost:9090/api/questionlistbyemail"

      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: state.token,
        token: state.token,
        'name': 'test list'
      }
      const body = { email: state.email }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status == 200) {
          state.lists = res.data
        } else {
          alert('bad.')
        }
      })
    }

    const deleteQuestion = async () => {
      const url = 'http://localhost:9090/api/deleteQuestion'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { questionid: state.qid, }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          setTimeout(questionlistbyemail(), 1000);
        }
      })
    }
    const modifyQuestion = async () => {
      if (state.title === '') {
        alert("Please input title"); title.value.focus(); return false;
      }
      if (state.content === '') {
        alert("Please input content"); content.value.focus(); return false;
      }

      const url = "http://localhost:9090/api/questionModify"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token,
      }
      const body = {
        questionid: modal.questionid,
        nickname: modal.nickname,
        title: modal.title,
        content: modal.content,
        questionPublic: questionPublic.value,
        modDate: state.modDate
      }
      const response = await axios.post(url, body, { headers })
      if (response.status === 200) {
        alert('modify successfully')
        setTimeout(questionlistbyemail(), 1000);
      } else {
        alert('fail')
      }
    }

    const regist = reactive({
      registContent: '',
      registTitle: ''
    });

    // const questionRegister = async() => {
    //     if(regist.registContent === ''){
    //       alert('Please Input your content');email.value.focus(); return false;
    //     }
    //     const url = 'http://localhost:9090/api/questionRegister'
    //     const headers = {
    //       "Content-Type" : "application/json",
    //       Authorization: state.token,
    //       "token": state.token
    //     }
    //     const body = {
    //       circleid : state.circleid,
    //       content : regist.registContent,
    //       questionPublic : questionPublic.value,
    //       title : regist.registTitle,
    //       uid : state.uid,
    //     }
    //     const response = await axios.post(url, body, {headers})
    //     if(response.status === 200){
    //       alert('register successfully');
    //       state.showModal=false
    //       setTimeout(questionlistbyemail(),1000);
    //     } else {
    //       alert('fail')
    //     }
    //   }

    return { myoption, string2search, launchSearch, questionPublic, sendId, modal, formatDate, deleteModal, modifyQuestion, state, questionlistbyemail, deleteQuestion, regist }

  }
}



</script>

